<template>
  <div class="email-wrapper">
    <div class="center_block">
      <Nav />
      <div class="content">
        <div class="title-wrapper">
          <span>账号安全</span>
        </div>
        <div class="body-wrapper">
          <div class="title">
            <span>修改邮箱</span>
          </div>
          <el-form
            :model="ruleForm"
            status-icon
            :rules="rules"
            ref="ruleForm"
            label-width="100px"
            class="demo-ruleForm"
          >
            <el-form-item label="邮箱" prop="email">
              <el-input
                type="password"
                v-model="ruleForm.email"
                autocomplete="off"
                placeholder="请输入您的邮箱"
              ></el-input>
            </el-form-item>
            <el-form-item label="图形验证码" prop="graphicsCode">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input
                    type="password"
                    v-model="ruleForm.graphicsCode"
                    autocomplete="off"
                    placeholder="输入图形验证码"
                  ></el-input>
                </div>
                <div class="right-wrapper">reyui</div>
              </div>
            </el-form-item>
            <el-form-item label="验证码" prop="code">
              <div class="item-wrapper">
                <div class="el-input-wrapper">
                  <el-input
                    type="password"
                    v-model="ruleForm.code"
                    autocomplete="off"
                    placeholder="输入验证码"
                  ></el-input>
                </div>
                <div class="right-wrapper">ggth</div>
              </div>
            </el-form-item>
            <el-form-item>
              <div class="item-wrapper">
                <el-button type="warning" @click="submitForm('ruleForm')">确定</el-button>
                <el-button @click="$router.push({path:'account',query:{name:'security'}})">返回</el-button>
              </div>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav";
export default {
  components: {
    Nav
  },
  props: {},
  data() {
    return {
        ruleForm:{},
        rules:{}
    };
  },
  watch: {},
  computed: {},
  methods: {},
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
@import "../../../assets/styles/pc/index";
.email-wrapper {
  width: 100%;
  height: 100%;
  background-color: #fff;
  display: flex;
}
</style>


